// Small Carosel Bannner
.PortdexAI-small-slider {
    .inner {
        background: var(--color-secondary);
        display: flex;
        border-radius: var(--radius);
        justify-content: space-between;
        overflow: hidden;
        align-items: flex-end;

        @media #{$sm-layout} {
            flex-direction: column;
            align-items: flex-start;
        }

        &.bg-one {
            background: var(--color-secondary);
        }

        &.bg-two {
            background: var(--color-primary);
        }

        &.bg-three {
            background: var(--color-primary-gradient-start);
        }

        &.bg-four {
            background: var(--color-primary-alt);
        }

        .content {
            padding: 50px 30px;
            padding-right: 0;

            .title {
                margin-bottom: 10px;
                color: var(--color-white);
            }

            p {
                color: var(--color-white);
            }
        }
    }

    .img-section {
        display: flex;
        max-width: 50%;
        height: 100%;

        img {
            height: 100%;
            max-height: 165px;
        }

        @media #{$sm-layout} {
            max-width: 100%;
        }
    }
}

.rbt-dashboard-content {
    .slick-dots {
        display: flex;
        position: absolute;
        bottom: 30px;
        left: 40px;
    }

    .rainbow-slick-dot .slick-dots li button::after {
        background: var(--color-white);
        box-shadow: inset 0 0 0 5px var(--color-white);
        opacity: 0.6;
    }

    .rainbow-slick-dot .slick-dots li.slick-active button::after {
        box-shadow: inset 0 0 0 1px var(--color-white);
        opacity: 0.8;
    }
}

.welcome-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    .content-section {
        display: flex;
        align-items: center;

        .icon {
            font-size: var(--h3);
            margin-right: 10px;
        }

        .title {
            margin-bottom: 0;
        }
    }

    .btn-default {
        cursor: pointer;

        @media #{$small-mobile} {
            margin-top: 20px;
        }
    }
}



/*-------------------------
    Admin User Settings
--------------------------*/


.rbt-admin-profile {
    display: flex;

    .admin-thumbnail {
        margin-right: 12px;

        img {
            width: 40px;
            min-width: 40px;
            height: 40px;
            border-radius: 100%;
            border: 2px solid var(--color-border);
            padding: 3px;

            @media #{$small-mobile} {
                width: 30px;
                min-width: 30px;
                height: 30px;
                padding: 0;
            }
        }
    }

    .admin-info {
        display: flex;
        flex-wrap: wrap;

        .name {
            color: var(--color-heading);
            font-weight: 500;
            display: block;
            line-height: 1;
            margin-bottom: 2px;
            font-size: var(--font-size-b1);

            @media #{$large-mobile} {
                flex-basis: 100%;
                text-align: left;
            }
        }

        .rbt-btn-link {
            font-size: var(--font-size-b3);
            color: var(--color-primary);
            position: relative;

            &::after {
                position: absolute;
                content: "";
                left: 0;
                bottom: 0;
                background: var(--color-primary);
                width: 0;
                height: 2px;
                transition: 0.3s;
            }

            &:hover {
                &::after {
                    width: 100%;
                }
            }
        }
    }
}

.rbt-user-menu-list-wrapper {
    .user-list-wrapper {
        margin: 0 -12px;
        list-style: none;
        padding: 0;

        li {
            margin: 0;

            a {
                display: flex;
                padding: 5px 12px;
                align-items: center;
                border-radius: 3px;
                color: var(--color-body);

                &.active {
                    color: var(--color-primary);
                    background: var(--color-dark);
                }

                img,
                i {
                    max-height: 17px;
                    margin-right: 7px;
                }

                i {
                    font-size: 17px;
                }

                span {
                    font-size: 14px;
                }
            }

            &:hover {
                a {
                    color: var(--color-primary);
                    background: var(--color-dark);
                }
            }
        }
    }
}

.rbt-user {
    height: 80px;
    line-height: 80px;
    margin: 0 17px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;

    @media #{$small-mobile} {
        padding: 0;
        margin: 0;
        margin-left: 0;
        margin-left: 3px;
    }
}

.rbt-user-wrapper {
    position: relative;
    z-index: 2;

    .admin-img {
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 2;
        border-radius: 100%;
        color: var(--color-primary);
        border: none;
        font-size: var(--font-size-b1);
        overflow: hidden;

        img {
            width: 40px;
            height: 40px;
            border-radius: 100%;
            border: 2px solid var(--color-border);
            padding: 3px;
            min-width: 40px;

            @media #{$small-mobile} {
                width: 35px;
                min-width: 35px;
                height: 35px;
                padding: 0;
            }
        }
    }

    @media #{$large-mobile} {
        position: static;
    }

    .rbt-user-menu-list-wrapper {
        @extend %rbt-dropdown-type-1;
        background: var(--color-blackest);
        min-width: 250px;
        border-radius: 0 0 10px 10px;
        border: 1px solid var(--color-border);
        border-top: none;

        @media #{$lg-layout} {
            left: auto;
            right: 0;
        }

        @media #{$md-layout} {
            left: auto;
            right: 0;
        }

        @media #{$sm-layout} {
            left: auto;
            right: 0;
        }

        @media #{$large-mobile} {
            left: 20px;
            right: 20px;
            width: auto;
        }

        .inner {
            padding: 25px 30px;

            hr {
                margin-left: -30px;
                margin-right: -30px;
            }

            .rbt-admin-profile {
                padding-bottom: 20px;
            }
        }
    }

    &:hover {
        .rbt-user-menu-list-wrapper {
            @extend %rbt-hover-dropdown-type-1;

        }
    }

    &.right-align-dropdown {
        .rbt-user-menu-list-wrapper {
            right: 0;
            left: auto;

            @media #{$large-mobile} {
                right: 20px;
                left: 20px;
                width: auto;
            }
        }
    }
}